import './App.css';
import Headerino from './header';
import InputMath from './inputmath';
import MathCanvas from './mathcanvas';
import React from 'react';
import ReactDOM from 'react-dom/client';
import  {evaluate, simplify,parse} from 'mathjs'
import { create, all } from 'mathjs'
import  InfiniteCanvas  from 'ef-infinite-canvas';
const config = { };
const math = create(all, config);

class App extends React.Component{
 constructor(props){
  super(props);
  this.state={
    equation:'1',
  }
  this.handleInput=this.handleInput.bind(this);
  this.handleCanvas=this.handleCanvas.bind(this);
 }
 handleCanvas=()=>{
  let y;
  let c=document.getElementById("c");
  let ctx = c.getContext("2d");
  let result=parse(this.state.equation);
  result=simplify(result).toString();
  ctx.strokeStyle="red";
  if (result) {
    ctx.beginPath();
    for(let x=-500;x<500;x++){
      y=math.evaluate(result,{x:x});
      console.log(x,y)
      if(y<1000){
      ctx.moveTo(-x+500,-y+500);
      ctx.lineTo(-x+500,-math.evaluate(result,{x:x+1})+500);
      }
    ctx.closePath();
    ctx.stroke()
    }
  }
  
}
 handleInput(e){
  this.setState({[e.target.name]:e.target.value})
  console.log(e.target.value)
 }
 
 render(){
  return (
    <div className="App">
      <Headerino />
      <InputMath name="equation" onClick={this.handleCanvas}onChange={this.handleInput} value={this.state.equation}/>
      <MathCanvas/>
    </div>
  );}
}

export default App;
