import { i } from 'mathjs';
import React from 'react';
import ReactDOM from 'react-dom/client';
import './mathcanvas.css';
import  InfiniteCanvas  from 'ef-infinite-canvas';
export default class MathCanvas extends React.Component{
    constructor(props){
        super(props);
    }
    componentDidMount(){
        let c=document.getElementById("c")
        let ctx = c.getContext("2d");
        ctx.strokeStyle='#999999';
        ctx.moveTo(250, 0);
        ctx.lineTo(250, 500);
        ctx.stroke();
        ctx.moveTo(0,250);
        ctx.lineTo(500,250);
        ctx.stroke();
        let j=0;
        for(let i=0;i<=1000;i+=50){
            ctx.moveTo(i,j);
            for(j=0;j<=1000;j+=50){
                ctx.lineTo(i,j);
                ctx.stroke();
            }
        for(let j=0;j<=1000;j+=50){
            ctx.moveTo(j,i);
            for(j=0;j<=1000;j+=50){
                    ctx.lineTo(j,i);
                    ctx.stroke();
                }}

    }
        
        
    }
    render(){
        return(
        <div className='canvasholder'>
            <canvas width="1000" height="1000" id="c">

            </canvas>
        </div>
        )
    }

}